body {
	direction: rtl;
	background-image: "./back.jpg";
	margin: 0;
	padding: 0;
}
.main-body-background {
	position: fixed;
	z-index: -100;
	max-width: 100%;
	/* max-height: 100%; */
}
.main-body-robot {
	width: 30%;
	position: fixed;
	top: 0px;
	left: 15rem;
}
.form-container {
	display: flex;
	flex-direction: column;
}
.form-container > textarea {
	resize: none;
	margin-bottom: 0.5rem;
}

.div-container {
	background-color: rgba(30, 144, 255, 0.5);
	padding: 1rem;
	width: 30%;
	position: fixed;
	bottom: 0px;
	right: 0px;
}
.label-input {
	flex-direction: row;
}
input {
	line-height: 1.5rem;
	margin-bottom: 0.5rem;
}
.form-header {
	color: rgb(43, 34, 62);
	text-align: center;
}
.submit-button {
	background-color: coral;
	border: none;
	color: beige;
	line-height: 1.5 rem;
	font-size: larger;
}
